.App {
  text-align: center; }

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
  font-size: 1.4em;
  font-family: cursive;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-align: center;
      align-items: center; }

.App__LoadIndicator {
  padding-top: 200px;
  font-size: 1.3em;
  font-weight: bold;
  margin-left: auto;
  background-color: #b6b6b6;
  background-image: url("./../movie.jpg");
  background-size: cover;
  background-attachment: fixed;
  height: 100vh; }

.App__NoResults {
  padding-top: 50px;
  font-size: 1.2em;
  margin-left: auto;
  background-color: #b6b6b6;
  background-image: url("./../movie.jpg");
  background-size: cover;
  background-attachment: fixed;
  text-shadow: 1px 1px 5px #ffffff;
  color: #b23238;
  height: 100vh; }

.App__Error {
  margin-top: 15px;
  margin-left: 15px;
  width: 400px;
  line-height: 25px;
  background-color: #f7c5cc;
  border-radius: 5px;
  border: 2px solid #ff4149;
  color: #9d0000;
  font-size: 1em; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40px;
  display: inline-block; }

.App-Title {
  display: inline-block;
  padding-left: 15px; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.Main__Search {
  background-color: #f7f7f7;
  border-radius: 5px;
  border: none;
  height: 20px;
  padding: 3px; }



